export default {
  name: "",
  props: [""],
  data() {
    return {
      searchHistoryItem: [],
      searchPeopleItem: []
    };
  },

  components: {},

  computed: {},

  beforeMount() {},

  mounted() {
    this.searchHistoryItem = JSON.parse(localStorage.getItem("searchHistory"));
  },

  methods: {
    delSearchHistory(){
      this.searchHistoryItem = [];
      localStorage.removeItem("searchHistory");
    }
  },

  watch: {}
};



